import React, {useContext} from 'react'
import  './Singlemealitem.css'
import Mealitemforms from './Mealitemforms';
import { AuthContext } from '../../../../shared/context/auth-context';
import Card from '../../../../shared/components/UIElements/Card'
import { NavLink } from 'react-router-dom';

const Singlemealitem = (props) => {
const cartctx= useContext(AuthContext)

    // const prices =`$${props.price.toFixed(2)}`

    const addtocarthandler = amount =>{
cartctx.additem({
  id: props.id,
  name:props.name,
  title:props.title,
  productcreator:props.productcreator,
  description:props.description,
  image:props.image,
  amount:amount,
  mrp:props.mrp,
  price:props.price
})
    }

  return (
   <>    
    <div className='product_div1 container'>    
    <img src={`${process.env.REACT_APP_ASSET_URL}/${props.image}`} alt={props.title} />  
    <div className='product_div_para1'> 
<h1 style={{fontSize:"15px", textAlign:'center'}}>{props.title}</h1>
<p style={{display:"flex", textAlign:'center'}}>{props.description}</p>
<h3 style={{fontSize:"15px", textAlign:'center', color:"red", textDecoration: "line-through"}}><span>Mrp: </span>{props.mrp}</h3>
<h3 style={{fontSize:"15px", textAlign:'center'}}><span>Offer Price: </span>{props.price}</h3>
</div>
<div className='form_input_div1'>          
<Mealitemforms onaddtocart={addtocarthandler}/>
</div>
</div>
   </>
  )
}

export default Singlemealitem
